.service-card {
  padding: 40px 20px;
  border: 1px solid #1cfdff;
  margin: 0 20px;
  border-radius: 10px;
  color: #1cfdff;
  box-shadow: 10px 10px 10px #1cfdff;
}

.service-card:hover {
  background-image: linear-gradient(to top right, #c339f8, #6093c2);
  box-shadow: 10px 10px 10px #6093c2;
  color: #fff;
  border: none;
}
